.shopCategory-banner{
    display: block;
    margin: 30px auto;
    width: 82%;

    
}
.shopcategory-indexsort{
    display: flex;
    margin: 0px 170px;
    justify-content: space-between ;


}
.shopcategory-sort{
    padding: 10px 20px ;
    border-radius: 40px;
    border: 1px solid#888;
}
.shopcategory-indexsort p span {
    font-weight: 600;
    
    
    
}
.shopcategory-products{
margin: 10px 20px;
display: grid;
grid-template-columns: 1fr 1fr 1fr 1fr;
row-gap: 10px;
}
.shopcategory-loadmore{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 75px auto;
    width: 233px;
    height: 69px;
    border-radius: 75px;
    background: #ededed;
    color: #787878;
    font-size:18px ;
    font-weight: 500;




}